import React, { useEffect, useState } from "react";
import { Button, Image, Empty, Toast, Form, Input,Card} from "antd-mobile";
// import * as mqtt2 from 'mqtt'.default
var mqtt2 = require('mqtt')
var clientTmp
var loadingToast
var allContent = ''
/**
 * 输入 host
 * 输入 port
 * 点击按钮开始链接
 * 接收的消息显示到输入框中。
 * 输入话题和内容，点击发送
 */
const Demo = () => {
  const [message, setMessage] = useState('')
  const [hasSubcribe, setHasSubcribe] = useState(false)
  function showMsg (str) {
    var now = new Date().toLocaleString()
    Toast.show({
      icon: 'success',
      content: str,
    })
    allContent = `[${now}]${str}` + '\n' + allContent
    setMessage(allContent)
  }
  // eslint-disable-next-line
  mqtt2 = mqtt

  // console.log('mqtt', mqtt2)
  const onConnect = (params) => {
    if(clientTmp) {
      clientTmp.end()
      showMsg('断开链接！')
      clientTmp = null
      return false
    }
    console.log('connect', params)
    clientTmp = mqtt2.connect(`mqtt://${params.host}`, {
      clientId: params.clientid,
      port: params.port
    })
    loadingToast = Toast.show({
      content: '开始链接服务器...',
      duration: 0
    })
    clientTmp.on('connect', function (msg) {
      showMsg('与服务器链接成功，开始接收消息')
      loadingToast.close()
      clientTmp.on('message', function (topic, message) {
        // message is Buffer
        console.log('收到消息:', topic, message.toString())
        showMsg( `收到话题：${topic}\n消息内容：${message.toString()}`)
      })
    })
  }
  const onListen = (params) => {
    if(clientTmp && clientTmp.subscribe) {
      if(hasSubcribe) {
        clientTmp.unsubscribe(params.topic, function (msg) {
          showMsg(`取消订阅话题${params.topic}成功`)
          setHasSubcribe(false)
        })
      } else {
        clientTmp.subscribe(params.topic, function (msg) {
          showMsg(`开始订阅话题${params.topic}成功`)
          setHasSubcribe(true)
        })
      }
    } else {
      Toast.show({
        icon: 'fail',
        content: '请先建立服务器链接',
      })
    }
  }

  const onSendMsg = (params) => {
    if(clientTmp && clientTmp.subscribe) {
      clientTmp.publish(params.topic, params.msg, function (msg) {
        showMsg(`成功发出消息：话题${params.topic}=>内容：${params.msg}`)
      })
    } else {
      Toast.show({
        icon: 'fail',
        content: '请先建立服务器链接',
      })
    }
  }

  return (
    <>
    <p>使用说明：</p>
    <p>1、使用websocket进行链接端口1884，非web直连使用1883端口。</p>
    <p>2、先输入域名端口号连接服务器，使用较独特的设备clientid</p>
    <p>3、输入要订阅的话题topic，进行监听其他地方广播出来的消息</p>
    <p>4、连接服务器后，输入要广播的话题和对应的内容，发送到服务器，供其他设备接收</p>
    <p>5、链接和收到消息，在页面底部会有打印日志</p>
    <Form
      layout='horizontal' mode='card'
      onFinish={onConnect}
      initialValues={{
        host: window.location.hostname,
        port: 1884,
        clientid: 'web-demo-' + new Date().getTime()
      }}
      footer={
        <Button block type='submit' color='primary' size='large'>
          {clientTmp ? '断开连接' : '开始连接'}
        </Button>
      }
    >
      <Form.Header>MQTT 设备测试demo</Form.Header>
      <Form.Item name='host' label='host'>
        <Input placeholder='请输入域名' />
      </Form.Item>
      <Form.Item name='port' label='port' >
        <Input placeholder='请输入端口' />
      </Form.Item>
      <Form.Item name='clientid' label='clientid'>
        <Input placeholder='请输入设备id' />
      </Form.Item>
    </Form>

    <Form
      layout='horizontal' mode='card'
      onFinish={onListen}
      initialValues={{
        topic: 'deviceId-feb20',
      }}
      footer={
        <Button block type='submit' color='primary' size='large'>
          {hasSubcribe ? '监听中...断开订阅': '开始订阅'}
        </Button>
      }
    >
      <Form.Header>消息订阅: 订阅话题、设备id</Form.Header>
      <Form.Item name='topic' label='topic' >
        <Input placeholder='请输入话题 或 设备id' />
      </Form.Item>
    </Form>

    <Form
      layout='horizontal' mode='card'
      onFinish={onSendMsg}
      initialValues={{
        topic: 'deviceId-feb20',
        msg: 'hello world',
      }}
      footer={
        <Button block type='submit' color='primary' size='large'>
          {clientTmp ? '发布消息' : '请先与服务器建立链接'}
        </Button>
      }
    >
      <Form.Header>广播消息: </Form.Header>
      <Form.Item name='topic' label='topic' >
        <Input placeholder='请输入话题 或 设备id' />
      </Form.Item>
      <Form.Item name='msg' label='消息内容' >
        <Input placeholder='请输入字符串类型消息内容' />
      </Form.Item>
    </Form>
    <Card title='消息日志记录：' >
      <div style={{
        whiteSpace: 'pre-line'
      }}>
      {message}
      </div>
    </Card>
    </>
  )
}
export default Demo
